.header {
  margin: 0 auto;
  max-width: 880px;
  padding: 45px 0 41px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 935px) {
  .header {
    padding: 45px 19px 41px;
  }
}

@media screen and (max-width: 490px) {
  .header {
    padding: 28px 27px 31px;
  }
}
