.profile__name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #fff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 490px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    max-width: 220px;
  }
}
